.app__gallery{
    display: flex;
    flex-direction: row;
    background: black;
    padding: 4rem 0 4rem 6rem;
}
.app__gallery-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 500px;
    padding-right: 2rem;
}
.app__gallery-content button{
    margin-top: 1rem;
}
.app__gallery-images{
    flex: 1;
    font-display: flex;
    max-width: 50%;
    flex-direction: row;
    position: relative;
}
.app__gallery-images_container{
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.app__gallery-images_container::-webkit-scrollbar{
    display: none;
} 
.app__gallery-images_card{
    position: relative;
    min-width: 301px;
    height: 447px;
    margin-right: 2rem;
}
.gallery__image-icon{
    color: white;
    font-size: 2rem;
    position: absolute;
    transition: 0.5s ease;
    opacity: 0.5;
    cursor: pointer;
}
.app__gallery-images_card img{
 width: 100%;
 object-fit: cover;
 opacity: 1;
 transition: 0.5s; 
 height: 100%;
}
.app__gallery-images_card:hover img{
   opacity: 0.35;
} 
.app__gallery-images_card:hover .gallery__image-icon{
   opacity: 1;
}
.app__gallery-images_arrows{
   width: 100%;
   display: flex;
   align-items: center;
   justify-content: space-between;
   bottom: 5%; 
   position: absolute; 
   padding: 0 1rem;
}
.gallery__arrow-icon{
   color: whitesmoke;
   cursor: pointer;
   font-size: 2rem;
   background: black;
   border-radius: 5px;
}
.gallery__arrow-icon:hover{
 color: rgb(155, 93, 13);
}

@media screen and (min-width:2000px) {
   .app__gallery-content{
       min-width: 1000px;
       padding-right: 4rem;
   }
   .app__gallery-images_card{
       min-height: 400px;
       height: 550px;
   }
}
@media screen and (max-width:1150px) {
   .app__gallery{
       flex-direction: column;
   }
   .app__gallery-content{
    width: 100%;
   }
   .app__gallery-images{
       max-width: 100%;
       margin: 5rem 0;

   }
}
@media screen and (max-width:850px) {
   .app__gallery{
       padding: 4rem 0 4rem 4rem ;
   }
}
@media screen and (max-width:650px) {
   .app__gallery{
       padding: 4rem 0 4rem 2rem;
   }
   .app__gallery-content{
       min-width: 100%;
   }
   .app__gallery-images_card{
       min-width: 240px;
       height: 320px;
   }
}

