.app__navbar{
    width: 100%;
    display: flex;
   justify-content: space-between;
   align-items: center;
   background:black;
   color:white;
   padding: 1rem 2rem;

}
.app__navbar-logo{
   display: flex;
   justify-content: flex-start;
   align-items: center;
}
.app__navbar-logo img{
    width: 150px;
}
.app__navbar-links{
    flex: 1;
    display: flex;
    justify-content: center;
    list-style: none;
    align-items: center;
}
.app__navbar-links li{
 margin: 0 1rem;
 cursor: pointer;
}
.app__navbar-links li:hover{
    color: rebeccapurple;
   }

.app__navbar-login{
    display: flex;
    align-items:center;
    justify-content: flex-end;
}
.app__navbar-login a{
   margin: 0 1rem;
   text-decoration: none; 
   transition: 0.5s ease;
}
.app__navbar-login a:hover{
    border-bottom: 1px solid rgb(--color-golden);
}
.app__navbar-login div{
    width: 1px;
    height: 30px;
    background-color: grey ;
}
.app__navbar-smallscreen{
    display: none;
}
.app__navbar-smallscreen_overlay{
    position: fixed; 
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: black;
    z-index: 5; 
    flex-direction: column;
    transition: 0.5s ease;
}
.app__navbar-smallscreen_overlay .overlay__close{
    font-size: 27px;
    color:antiquewhite; 
    cursor: pointer;
    top: 20px;
    right: 20px;
    position: absolute;
}
.app__navbar-smallscreen_links{
    list-style: none;
}
.app__navbar-smallscreen_links li{
    margin: 2rem;
    cursor: pointer;
    color: var(--color-golden);
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
    
}
.app__navbar-smallscreen_links li:hover{
    color: var(--color-white);
}

@media screen and (max-width:2000px) {
    .app__navbar-logo img{
        width: 210px;
    }
}

@media screen and (max-width:1150px) {
    .app__navbar-links{
        display: none;
    }
    .app__navbar-smallscreen{
        display: flex;
    }
}

@media screen and (max-width:650px) {
    .app__navbar{
        padding: 1rem;
    }
    .app__navbar-login{
        display: none;
    }
    .app__navbar-logo img{
        width: 110px;
    }
}